import { useEffect, useState, useContext } from "react";
import { Link, useParams  } from "react-router-dom";
import { useTranslation } from 'react-i18next';
import "bootstrap/dist/css/bootstrap.min.css";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { solid, brands } from '@fortawesome/fontawesome-svg-core/import.macro';
import { Swiper, SwiperSlide } from "swiper/react";
import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import { Autoplay, EffectFlip, Pagination, Parallax, Navigation } from "swiper";
import { useLoadingContext } from "react-router-loading";

import loadDappData from "./dapp/data.js";
import loadEffectData from "./effect/data.js";
import loadFullStackData from "./fullstack/data.js";
import Skeleton from 'react-loading-skeleton';
import 'react-loading-skeleton/dist/skeleton.css';

import Carousel from 'react-multi-carousel';
import 'react-multi-carousel/lib/styles.css';

const Home = () => {
  const { t, i18n } = useTranslation();
  const langUrl = i18n.language === '' ? '/en' : '/' + i18n.language;

  const [fullStackData, setFullStackData] = useState<any[]>();
  const [effectData, setEffectData] = useState<any[]>();
  const [dappData, setDappData] = useState<any[]>();
  const loadingContext = useLoadingContext();

  const loading = async () => {
    const dappData = await loadDappData();
    const effectData = await loadEffectData();
    const fullStackData = await loadFullStackData();

    setDappData(dappData);
    setEffectData(effectData);
    setFullStackData(fullStackData);

    loadingContext.done();
  };

  const responsive = {
    desktop: {
      breakpoint: { max: 3000, min: 1024 },
      items: 2,
      slidesToSlide: 1 // optional, default to 1.
    },
    tablet: {
      breakpoint: { max: 1024, min: 464 },
      items: 2,
      slidesToSlide: 1 // optional, default to 1.
    },
    mobile: {
      breakpoint: { max: 464, min: 0 },
      items: 1,
      slidesToSlide: 1 // optional, default to 1.
    }
  };

  useEffect(() => {
    loading();
  }, []);

  let loadingitems=['Item 1','Item 2'];

  return (
    <>
    {/*
      <div className="home-swiper-container">
        <div className="bg-dark pb-4">
          <Swiper
            spaceBetween={30}
            slidesPerView={1.1}
            centeredSlides={true}
            autoplay={{
              delay: 2500,
              disableOnInteraction: false,
            }}
            pagination={{
              el: '.home-swiper-pagination',
              clickable: true,
              renderBullet: (index, className) => {
              return '<span class="' + className + '"></span>';
              },
            }}
            parallax={true}
            loop={true}
            navigation={true}
            effect={"flip"}
            modules={[Autoplay, Pagination, Parallax, Navigation]}
            className="home-swiper"
          >
            <SwiperSlide>
              <img src={process.env.PUBLIC_URL + '/images/web-banner/1.webp'} className="card-img-top" alt="" />
            </SwiperSlide>
            <SwiperSlide>
              <img src={process.env.PUBLIC_URL + '/images/web-banner/2.webp'} className="card-img-top" alt="" />
            </SwiperSlide>
            <SwiperSlide>
              <img src={process.env.PUBLIC_URL + '/images/web-banner/3.webp'} className="card-img-top" alt="" />
            </SwiperSlide>
            <SwiperSlide>
              <img src={process.env.PUBLIC_URL + '/images/web-banner/4.webp'} className="card-img-top" alt="" />
            </SwiperSlide>
          </Swiper>
        </div>
        <div className="home-swiper-pagination" />
      </div>*/}

      <div className="home-top-container bg-dark text-light">
        <div className="container col-xxl-8 px-4 py-5">
          <div className="row flex-lg-row-reverse align-items-center g-5 py-5">
            <div className="col-12 col-sm-12 col-lg-6 text-center">
              <div className="rotate-react">
                <FontAwesomeIcon icon={brands('react')} size="10x" />
              </div>
            </div>
            <div className="col-lg-6">
              <h1 className="display-5 fw-bold lh-1 mb-3">Test React</h1>
              <p className="lead">React makes it painless to create interactive UIs. Design simple views for each state in your application, and React will efficiently update and render just the right components when your data changes.</p>
              <div className="d-grid gap-2 d-md-flex justify-content-md-start">
                <a href="https://reactjs.org/" target="_blank" rel="noreferrer" className="btn btn-primary btn-lg px-4 me-md-2">Reactjs.org</a>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className="container pb-3">

        <div className="mt-3 list row">
          <div className="col-md-12">
            <h3 className="mt-3">DAPP<Link to={langUrl + "/dapp"} className="btn btn-outline-primary btn-sm float-end">
              {t('home.more')} <FontAwesomeIcon icon={solid('ellipsis')} />
            </Link></h3>
          </div>
        </div>
        <div className="list row">
          <Carousel responsive={responsive} className="p-0">
            {dappData
              ? (dappData).filter(({Status}) => Status === true).map((item,index) => (
                <div className="p-2" key={index}>
                  <div className="card">
                    {item['Blank']
                      ? <a href={item['Url']} target="_blank" rel="noopener noreferrer">
                          <img src={process.env.PUBLIC_URL + item['Image']} className="card-img-top" alt={item['Name']} />
                        </a>
                      : <Link to={langUrl + item['Url']}>
                          <img src={process.env.PUBLIC_URL + item['Image']} className="card-img-top" alt={item['Name']} />
                        </Link>
                    }
                    <div className="card-body">
                      <h5 className="card-title text-center mb-3">{item['Name']}</h5>
                      <div className="text-center">
                        {item['Blank']
                          ? <a href={item['Url']} className="btn btn-primary" target="_blank" rel="noopener noreferrer">
                              {t('home.view_more')} <FontAwesomeIcon icon={solid('arrow-right')} />
                            </a>
                          : <Link to={langUrl + item['Url']} className="btn btn-primary">
                              {t('home.view_more')} <FontAwesomeIcon icon={solid('arrow-right')} />
                            </Link>
                        }
                      </div>
                    </div>
                  </div>
                </div>
                )
              )
              : (loadingitems).map((loadingitems,index)=>(
                <div className="p-2" key={index}>
                  <div className="card">
                    <Skeleton height={300} />
                    <div className="card-body">
                      <h5 className="card-title"><Skeleton /></h5>
                    </div>
                  </div>
                </div>
              ))
            }
          </Carousel>
        </div>

        <div className="mt-3 list row">
          <div className="col-md-12">
            <h3 className="mt-3">Effect<Link to={langUrl + "/effect/tsparticles"} className="btn btn-outline-primary btn-sm float-end">
              {t('home.more')} <FontAwesomeIcon icon={solid('ellipsis')} />
            </Link></h3>
          </div>
        </div>
        <div className="list row">
          {effectData
            ? (effectData).filter(({Status}) => Status === true).map((item,index) => (
              <div className="col-md-6 p-2" key={index}>
                <div className="card">
                  <Link to={langUrl + item['Url']}>
                    <img src={process.env.PUBLIC_URL + item['Image']} className="card-img-top" alt={item['Name']} />
                  </Link>
                  <div className="card-body">
                    <h5 className="card-title text-center mb-3">{item['Name']}</h5>
                    <div className="text-center">
                      <Link to={langUrl + item['Url']} className="btn btn-primary">
                      {t('home.view_more')} <FontAwesomeIcon icon={solid('arrow-right')} />
                      </Link>
                    </div>
                  </div>
                </div>
              </div>
              )
            )
            : (loadingitems).map((loadingitems,index)=>(
              <div className="col-md-6 p-2" key={index}>
                <div className="card">
                  <Skeleton height={300} />
                  <div className="card-body">
                    <h5 className="card-title"><Skeleton /></h5>
                  </div>
                </div>
              </div>
            ))
          }
        </div>

        <div className="mt-3 list row">
          <div className="col-md-12">
            <h3 className="mt-3">Full Stack<Link to={langUrl + "/fullstack"} className="btn btn-outline-primary btn-sm float-end">
              {t('home.more')} <FontAwesomeIcon icon={solid('ellipsis')} />
            </Link></h3>
          </div>
        </div>
        <div className="list row">
          {fullStackData
            ? (fullStackData).filter(({Status}) => Status === true).map((item,index) => (
              <div className="col-md-6 p-2" key={index}>
                <div className="card">
                  <a href={item['Url']} target="_blank" rel="noopener noreferrer">
                    <img src={process.env.PUBLIC_URL + item['Image']} className="card-img-top" alt={item['Name']} />
                  </a>
                  <div className="card-body">
                    <h5 className="card-title text-center mb-3">{item['Name']}</h5>
                    <div className="text-center">
                      <a href={item['Url']} target="_blank" rel="noopener noreferrer" className="btn btn-primary">{t('home.view_more')} <FontAwesomeIcon icon={solid('arrow-right')} /></a>
                    </div>
                  </div>
                </div>
              </div>
              )
            )
            : (loadingitems).map((loadingitems,index)=>(
              <div className="col-md-6 p-2" key={index}>
                <div className="card">
                  <Skeleton height={300} />
                  <div className="card-body">
                    <h5 className="card-title"><Skeleton /></h5>
                  </div>
                </div>
              </div>
            ))
          }
        </div>


        
        

        
      </div>
    </>
  )
}

export default Home