import { Component, ChangeEvent } from "react";
import { Link } from "react-router-dom";
import { useTranslation } from 'react-i18next';

const WalletConnect = () => {
  //#region State
  //#endregion
  
  const { t, i18n } = useTranslation();
  const langUrl = i18n.language === '' ? '/en' : '/' + i18n.language;

  return (
    <>
    <div className="container">
      <div className="mt-3 list row">
        <div className="col-md-12">
          <p className="mt-3 mb-3 page-navigator">
          <Link to={langUrl}>{t('home.title')}</Link> / <Link to={langUrl + "/dapp"}>DAPP</Link> / <Link to={langUrl + "/dapp/walletconnect"}>WalletConnect</Link>
          </p>
          <h4 className="text-center">WalletConnect</h4>
        </div>
      </div>
      <div className="mt-3 list row">
        <div className="col-md-12">
        </div>
      </div>
    </div>
    </>
  );
}

export default WalletConnect